	module.exports = {"data":{"site":{"siteMetadata":{"title":"Justin Ball","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2006-11-08-ray-ozzie/index.md absPath of file >>> MarkdownRemark","html":"<p>John Battelle “How has the world changed and how is it going a year after the memo from Bill Gates?”</p>\n<p>Ray – It is going very well. Some people get it some don’t. Vista and Office are done – released to manufacturing. Should be out to business by the end of November. Vista should be more secure, prevent trojans, phishing etc.</p>\n<p>There is still a need for desktop software. The web isn’t always there. Sometimes the pipe isn’t big enough.</p>\n<p>I have heard about Ray Ozzie. He is supposed to be a big deal, but I didn't get much out of what he had to say and in fact it felt to me like he had turned into a Microsoft Apologist. I guess that makes sense since he is chief software architect at Microsoft. He is an admirable man with many accomplishments so perhaps he will be the one to lead Microsoft to its next version.</p>","frontmatter":{"title":"Ray Ozzie","date":"November 08, 2006","imageUrl":null,"image":null}}},"pathContext":{"path":"/ray-ozzie"}}

